<template>
  <div class="container">企业名称：
    <el-input class="com-input" placeholder="请输入内容" v-model="comRecheckParams.name"></el-input>修改人：
    <el-input class="com-input" placeholder="请输入内容" v-model="comRecheckParams.operatorId"></el-input>状态：
    <el-select v-model="comRecheckParams.status" placeholder="全部">
      <el-option
        v-for="item in checkStatus"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      ></el-option>
    </el-select>
    <el-button type="primary" icon="el-icon-search" class="btn" @click="search">查询</el-button>
    <el-main>
      <el-table :data="comInfoList">
        <el-table-column prop="id" label="ID"></el-table-column>
        <el-table-column prop="name" label="企业名字"></el-table-column>
        <el-table-column prop="address" label="企业地址"></el-table-column>
        <el-table-column prop="operatorName" label="修改人"></el-table-column>
        <el-table-column prop="updateTime" label="修改时间" width="200"></el-table-column>
        <el-table-column prop="reason" label="修改原因"></el-table-column>
        <el-table-column prop="status" label="状态"></el-table-column>
        <el-table-column label="操作" width="220">
          <template slot-scope="scope">
            <el-button @click="checkDetail(scope.row)" type="text" size="small">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination v-show="pageShow"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
  </div>
</template>

<script>
const queryString = require('querystring')
import { checkStatus, comRecheckParams } from 'data/recheck'
export default {
    name: 'comList',
    data() {
        return {
          checkStatus,
          comRecheckParams,
          // currentPage: this.$router.query.currentPage || 1,
          currentPage: 1,
          totalPage: 0,
          totalCount: 0,
          pageSize: 20,
          pageShow: false,
          comInfoList: []
        }
    },
    props: {
      status: {
        default: false,
        type: Boolean
      }
    },
    created() {
      if (this.status) this.search()
    },
    methods: {
        search() {
          this.comRecheckParams.page = this.currentPage - 1
          this.axios.get('/company/tempList?' + queryString.stringify(this.comRecheckParams)).then((res) => {
              const code = Number(res.data.code)
              const data = res.data.data
              if (code === 200) {
                  this.comInfoList = data.list
                  this.pageShow = data.totalCount > 20
                  this.totalPage = data.totalPage
                  this.pageSize = this.comRecheckParams.size
                  this.totalCount = data.totalCount
              } else {
                  this.$message(this.mes.busy)
              }
            }).catch((err) => {
              console.log(err)
              this.$message(this.mes.busy)
            })
        },
        checkDetail(item) {
          this.$router.push('comInfo?id=' + item.id + '&currentPage=' + this.currentPage)
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
          },
        handleCurrentChange(val) {
          console.log(`当前页: ${val}`);
          this.currentPage = val
        }
    },
    watch: {
      currentPage() {
        this.search()
      }
    }
}
</script>

<style lang="less" scoped>
@import url('../../../../assets/less/checklist.less');
</style>
