<template>
  <div class="container">商品名称：
    <el-input class="com-input" placeholder="请输入内容" v-model="goodsRecheckParams.name"></el-input>修改人：
    <el-input class="com-input" placeholder="请输入内容" v-model="goodsRecheckParams.operatorId"></el-input>状态：
    <el-select v-model="goodsRecheckParams.status" placeholder="全部">
      <el-option
        v-for="item in checkStatus"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      ></el-option>
    </el-select>
    <el-button type="primary" icon="el-icon-search" class="btn" @click="search">查询</el-button>
    <el-main>
      <el-table :data="goodsInfoList">
        <el-table-column prop="id" label="ID"></el-table-column>
        <el-table-column prop="name" label="商品名称"></el-table-column>
        <el-table-column prop="ptGoodsPlanName" label="所属计划"></el-table-column>
        <el-table-column prop="property" label="修改人"></el-table-column>
        <el-table-column prop="updateTime" label="修改时间"></el-table-column>
        <el-table-column prop="reason" label="修改原因"></el-table-column>
        <el-table-column prop="operatorType" label="类型"></el-table-column>
        <el-table-column prop="status" label="状态"></el-table-column>
        <el-table-column label="操作" width="220">
          <template slot-scope="scope">
            <el-button @click="checkDetail(scope.row)" type="text" size="small">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000"
        ></el-pagination>
      </div>
    </el-main>
  </div>
</template>

<script>
const queryString = require('querystring')
import { checkStatus, goodsRecheckParams } from 'data/recheck'
export default {
  name: "goodsList",
  data() {
    return {
      currentPage: 0,
      goodsRecheckParams,
      checkStatus,
      pageShow: false,
      goodsInfoList: []
    };
  },
  props: {
      status: {
        default: false,
        type: Boolean
      }
    },
    created() {
      if (this.status) this.search()
    },
  methods: {
    search() {
      this.axios.get('/goods/tempList?' + queryString.stringify(this.goodsRecheckParams)).then((res) => {
        const code = Number(res.data.code)
        if (code === 200) {
            this.goodsInfoList = res.data.data.list
            if (this.goodsInfoList.length === 0) this.$message(this.mes.noData)
        } else {
            this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    },
    checkDetail(item) {
      this.$router.push('serveInfo?id=' + item.id + '&operatorType=' + item.operatorType)
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    checkDetail(item) {
      this.$router.push('goodsInfo?id=' + item.id)
    },
  }
};
</script>

<style lang="less" scoped>
@import url('../../../../assets/less/checklist.less');
</style>